@import './../../../styles/sass/vars.scss';
@import './../../../styles/sass/mixins.scss';

.main {
    background-image: url(./../../../assets/img/main-bg.jpg);
    color: $primaryColor;
    background-size: cover;
    background-position: 62% center;

    @include media(sm) {
        background-size: 235%;
        background-position: 56% center;
    }

    @include media(md) {
        background-size: 141%;
        background-position: 50% center;
    }

    @include media(lg) {
        background-size: 134%;
    }

    @include media(xxl) {
        background-size: 100%;
    }
}

.container {
    @extend %container;
}

.inner {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 655px;
    padding-top: 143px;
    padding-bottom: 149px;

    @include media(xs) {
        padding-top: 154px;
        padding-bottom: 140px;
    }

    @include media(sm) {
        min-height: 840px;
        padding-top: 285px;
        padding-bottom: 252px;
    }
}

// @media screen and (min-width: 768px){
//     height: 840px;
// }

.title {
    @include text(32px, 700, 1.2);
    max-width: 247px;

    span {
        color: $accentColor;
    }

    // @media screen and (min-width: 768px){
    //     max-width: 596px;
    //     font-size: 40px;
    //     line-height: 49px;
    // }
    @include media(sm) {
        max-width: 596px;
        font-size: 40px;
        line-height: 1.23;
    }

    // @media screen and (min-width: 1280px){
    //     max-width: 887px;
    //     width: 100%;
    // }
    @include media(md) {
        max-width: 887px;
        width: 100%;
    }
}

.priceBox {
    height: 93px;
    max-width: 208px;
    background-image: url(./../../../assets/img/price-box.svg);
    background-repeat: no-repeat;
    background-size: cover;
    margin: 36px 0 14px;
    display: flex;
    justify-content: space-around;
    padding: 6px 15px;
    margin-left: -5px;

    @include media(sm) {
        height: 96px;
        max-width: 224px;
        margin: 14px 0 14px;
        padding: 11px 29px 10px 22px;
        margin-left: -2px;
        background-size: 105%;
    }

    &Wrap {
        display: flex;
        flex-direction: column;
    }
}

.priceLabel {
    @include text(18px, 400, inherit);
    margin-bottom: 3px;
    margin-top: 0;

    @include media(sm) {
        @include text(20px, 400, inherit);
        margin-bottom: 0;
    }
}

.price {
    @include text(30px, 800, 1.27);

    @include media(sm) {
        @include text(35px, 800, 1.27);
    }
}


.percentWrap {
    border-radius: 50%;
    width: 47px;
    height: 47px;
    margin-top: -3px;
    transform: translateX(-11px);


    @include media(sm) {
        width: 52px;
        height: 52px;
        margin-top: -7px;
        transform: translateX(-13px);
    }
}

.percent {
    filter: drop-shadow(0px 4px 15px rgba(255, 255, 255, 0.25)) drop-shadow(0px 4px 4px rgba(255, 255, 255, 0.25));
    object-fit: cover;
    width: 165%;
}